import React, { createContext, useContext, useState } from "react";
// Create a context with an initial undefined value
const AppContext = createContext(undefined);
// Custom hook to use the AppContext
export const useAppContext = () => {
    const context = useContext(AppContext);
    if (!context) {
        throw new Error("useAppContext must be used within an AppProvider");
    }
    return context;
};
const vp = {
    name: "",
    nickname: "",
    gender: "",
    birthday: "",
    email: "",
    cardNum: "",
    landlineNum: "",
    phoneNum: "",
    address: "",
    companyName: "",
    companyAddress: "",
    companyPhoneNum: "",
    custom1: "",
    custom2: "",
    custom3: "",
    age: "",
    country: "",
    city: "",
    expirationDate: "",
    cvv: "",
    cardNetwork: "",
    username: "",
    userId: "",
    jobTitle: "",
    industry: "",
};
const mo = {
    noteId: "",
    designStatusTagId: "",
    titleSectionId: "",
};
const lm = {
    tier: "FREE",
    isLicenseActive: false,
    licenseKey: "",
    sessionExpiredAt: "",
};
// Provider component
export const AppProvider = ({ children }) => {
    // #region useState
    // 訂閱呼籲用
    const [showCTSubscribe, setShowCTSubscribe] = useState(false);
    const [showActivateModal, setShowActivateModal] = useState(false);
    // 模組用
    const [lastCustomSpacing, setLastCustomSpacing] = useState("");
    const [memorizedObjectWidth, setMemorizedObjectWidth] = useState(0);
    const [memorizedObjectHeight, setMemorizedObjectHeight] = useState(0);
    const [memorizedObjectName, setMemorizedObjectName] = useState("");
    const [variableCollectionList, setVariableCollectionList] = useState([]);
    const [variableCollectionModes, setvariableCollectionModes] = useState([]);
    const [virtualProfile, setVirtualProfile] = useState(vp);
    const [magicalObject, setMagicalObject] = useState(mo);
    const [licenseManagement, setLicenseManagement] = useState(lm);
    // #region JSX elements
    return (React.createElement(AppContext.Provider, { value: {
            lastCustomSpacing,
            setLastCustomSpacing,
            memorizedObjectWidth,
            setMemorizedObjectWidth,
            memorizedObjectHeight,
            setMemorizedObjectHeight,
            memorizedObjectName,
            setMemorizedObjectName,
            variableCollectionList,
            setVariableCollectionList,
            variableCollectionModes,
            setvariableCollectionModes,
            virtualProfile,
            setVirtualProfile,
            magicalObject,
            setMagicalObject,
            licenseManagement,
            setLicenseManagement,
            showCTSubscribe,
            setShowCTSubscribe,
            showActivateModal,
            setShowActivateModal,
        } }, children));
};
