// 取代原有的 fundamental-module.ts
export function deepClone(val) {
    return JSON.parse(JSON.stringify(val));
}
export function sendMessageBack(message) {
    // console.log("Message sent back");
    // console.log(message);
    figma.ui.postMessage({
        pluginMessage: message,
    });
}
export function rgbToHex(r, g, b, withHashTag = true) {
    // Ensure the RGB values are within the valid range
    r = Math.round(r * 255);
    g = Math.round(g * 255);
    b = Math.round(b * 255);
    // Convert each component to a 2-digit hexadecimal string
    const hexR = r.toString(16).padStart(2, "0");
    const hexG = g.toString(16).padStart(2, "0");
    const hexB = b.toString(16).padStart(2, "0");
    if (withHashTag) {
        // Concatenate the hex components and prepend a '#'
        return `#${hexR}${hexG}${hexB}`;
    }
    else {
        // Concatenate the hex components and prepend a '#'
        return `${hexR}${hexG}${hexB}`;
    }
}
export function rgbToRGB255(r, g, b) {
    const newR = Math.round(r * 255);
    const newG = Math.round(g * 255);
    const newB = Math.round(b * 255);
    return `R${newR}, G${newG}, B${newB}`;
}
// Function to get the current viewport and return it as a Vector type
export function getCurrentViewport() {
    // Get the current viewport
    const viewport = figma.viewport;
    // Create a Vector object with the current viewport center coordinates
    const vector = {
        x: Math.round(viewport.center.x),
        y: Math.round(viewport.center.y),
    };
    return vector;
}
export function getCurrentSelection() {
    // Get an array of the currently selected layers
    return [...figma.currentPage.selection];
}
export function getNodesInSelectedFrame() {
    const selectedNode = figma.currentPage.selection[0];
    if (selectedNode && selectedNode.type === "FRAME") {
        return [...selectedNode.children];
    }
    return null;
}
export function isNodeWithResizeMethod(node) {
    return (node.type === "FRAME" ||
        node.type === "STAR" ||
        node.type === "ELLIPSE" ||
        node.type === "POLYGON" ||
        node.type === "RECTANGLE");
}
export function mapToUnitRange(value) {
    if (value < 0) {
        return 0;
    }
    else if (value > 255) {
        return 1;
    }
    else {
        return value / 255;
    }
}
export function getBoundingBox(layers) {
    let minX = Infinity;
    let minY = Infinity;
    let maxX = -Infinity;
    let maxY = -Infinity;
    layers.forEach((layer) => {
        const { x, y, width, height } = layer;
        if (x < minX)
            minX = x;
        if (y < minY)
            minY = y;
        if (x + width > maxX)
            maxX = x + width;
        if (y + height > maxY)
            maxY = y + height;
    });
    const width = maxX - minX;
    const height = maxY - minY;
    return { width, height };
}
export function getSelectionPosition(layers) {
    let minX = Infinity;
    let minY = Infinity;
    layers.forEach((layer) => {
        const { x, y } = layer;
        if (x < minX)
            minX = x;
        if (y < minY)
            minY = y;
    });
    return { x: minX, y: minY };
}
export function hasImageFill(node) {
    return (Array.isArray(node.fills) &&
        node.fills.some((fill) => fill.type === "IMAGE"));
}
// Utility function to convert RGBA from 0-255 range to 0-1 range
export function convertColorRange(rgba) {
    return {
        r: rgba.r / 255,
        g: rgba.g / 255,
        b: rgba.b / 255,
        a: rgba.a,
    };
}
export function hexToRgb(hex) {
    // Remove the hash at the start if it's there
    hex = hex.replace(/^#/, "");
    // Parse the r, g, b values
    const bigint = parseInt(hex, 16);
    const r = ((bigint >> 16) & 255) / 255;
    const g = ((bigint >> 8) & 255) / 255;
    const b = (bigint & 255) / 255;
    return { r, g, b };
}
// Helper function to format the date as MM/DD
export function getFormattedDate() {
    const today = new Date();
    const month = String(today.getMonth() + 1).padStart(2, "0"); // Months are zero-based
    const day = String(today.getDate()).padStart(2, "0");
    return `${month}/${day}`;
}
/**
 * Adds a specified number of hours to the given date.
 * @param date The original date.
 * @param hours The number of hours to add.
 * @returns A new date object with the hours added.
 */
export function addHours(date, hours) {
    // Create a new date object based on the provided date
    const resultDate = new Date(date);
    // Add the specified number of hours
    resultDate.setHours(resultDate.getHours() + hours);
    // Return the modified date
    return resultDate;
}
export function convertUTCStringToDate(utcString) {
    return new Date(utcString);
}
